@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  background-color: #232533;
}

.App {
  text-align: center;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  color: white;
}

* {
  text-align: center;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: white;
}

body {
  margin: unset;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #3292ff;
}

::-webkit-scrollbar-track {
  background-color: #282a39;
}

#header {
  position: absolute;
  height: 90px;
  top: 0;
  width: 100vw;
}

#header-container {
  display: flex;
  flex-direction: row;
  padding-left: 112px;
  padding-right: 152px;
  width: 100%;
  height: 100%;
  align-items: center;
  box-sizing: border-box;
  justify-content: space-between;
}

#logo {
  height: 30px;
}

#header-container > ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  margin: unset;
}

#header-container > ul > a {
  padding: 0 17px;
  transition: color 250ms;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

#header-container > ul > a.selected {
  color: #3292ff;
}

.arrow-down {
  transition: fill 250ms;
}

#header-container > ul > a:hover .arrow-down {
  fill: #3292ff;
}

#header-container > ul > a.selected.scrolled {
  border-bottom: 2px solid #3292ff;
}

#header-container > ul > a:hover {
  color: #3292ff;
  cursor: pointer;
}

a > span {
  color: inherit;
}

#home {
  display: flex;
  flex-direction: column;
  padding-top: 224px;
  grid-gap: 2rem;
  gap: 2rem;
}

.content-section {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.info-card {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.info-card > .title {
  font-size: 1.8rem;
}

.info-card > .content {
  box-shadow: 0px 0px 20px 10px rgba(0, 0, 0, 0.25);
  padding: 25px;
  height: 470px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

#motd {
  min-width: 400px;
  max-width: 600px;
}

li {
  text-align: left;
}

.info-card > .content > h2 {
  margin: unset;
}

.pick-container {
  padding: unset !important;
}

#anime {
  display: flex;
  flex-direction: column;
  padding-top: 224px;
  grid-gap: 2rem;
  gap: 2rem;
  width: 80%;
  max-width: 1200px;
}

#anime-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#anime-details {
  display: flex;
  flex-direction: row;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100%;
}

#details {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.blue {
  color: #3292ff;
}

#details * {
  text-align: left;
}

#release-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.label {
  padding: 2px 4px;
  border-radius: 4px;
}

.success {
  background-color: #45d957;
}

.neutral {
  background-color: gray;
}

#stream-button {
  padding: 15px 80px;
  background-color: #3292ff;
  box-sizing: border-box;
  border-radius: 4px 4px 0 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.semi-bold {
  font-weight: 600;
}

#stream-information {
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.1);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 0 0 4px 4px;
}

#air-information {
  padding: 20px;
}

#air-information > div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#cover-container > img {
  border-radius: 4px;
}

#description-text {
  color: rgba(255, 255, 255, 0.85);
}

#description-synopsis {
  color: rgba(255, 255, 255, 0.7);
}

#player-page {
  display: flex;
  flex-direction: column;
  padding-top: 224px;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#player-container {
  width: 1100px;
}

#player {
  min-height: 0px;
  display: block;
  width: 100%;
  height: 620px;
}

#search {
  display: flex;
  flex-direction: column;
  padding: 224px 100px 0 100px;
  grid-gap: 2rem;
  gap: 2rem;
  align-items: center;
}

#search-input {
  all: unset;
  border-bottom: 2px solid white;
  width: 100%;
  font-size: 20px;
  transition: border-bottom 250ms;
}

#search-input:hover {
  border-bottom: 2px solid #3292ff;
}

.anime-entry {
  height: 350px;
  width: 250px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column-reverse;
}

.anime-entry > .info-container {
  width: 100%;
  height: 75px;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px;
  box-sizing: border-box;
}

.info-container > span {
  text-align: left;
}

.info-container > .anime-status {
  color: #3292ff;
}

#entries-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

