#header {
  position: absolute;
  height: 90px;
  top: 0;
  width: 100vw;
}

#header-container {
  display: flex;
  flex-direction: row;
  padding-left: 112px;
  padding-right: 152px;
  width: 100%;
  height: 100%;
  align-items: center;
  box-sizing: border-box;
  justify-content: space-between;
}

#logo {
  height: 30px;
}

#header-container > ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  margin: unset;
}

#header-container > ul > a {
  padding: 0 17px;
  transition: color 250ms;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

#header-container > ul > a.selected {
  color: #3292ff;
}

.arrow-down {
  transition: fill 250ms;
}

#header-container > ul > a:hover .arrow-down {
  fill: #3292ff;
}

#header-container > ul > a.selected.scrolled {
  border-bottom: 2px solid #3292ff;
}

#header-container > ul > a:hover {
  color: #3292ff;
  cursor: pointer;
}

a > span {
  color: inherit;
}
